import { Component, OnInit } from '@angular/core';
import {AppContextService} from "../../../services/app-context.service";
import {Message} from "../../../Classes/Classes";

@Component({
  selector: 'app-video-player',
  templateUrl: './video-player.component.html',
  styleUrls: ['./video-player.component.css']
})
export class VideoPlayerComponent implements OnInit {

    public dataLoaded = false;
    public activeVideoBlocks = [];
    public videoMetadata : {videoStart : boolean, src : string, uid : string, local : boolean, className : any, t : number}[];
  
    constructor(public appContext : AppContextService) {
      let metadata  = Object.values(this.appContext.activeMessage.metadata),
	  minTime = Math.min(...metadata.map((mess : any) => {
	      return mess.startTime;
	  }));
      this.videoMetadata = metadata.map((mess : any) => {
          let data : any = {
              startTime : mess.startTime,
	      visual : mess.visual,
              videoStart : false,
	      src : mess.downLoadedUrl,
	      uid : mess.uid ,
	      local : mess.local,
	      contact : mess.local ? this.appContext.activeMessage.sender : this.appContext.activeMessage.receivers.find(res => res.uid === mess.uid ),
	      className : {local : mess.local, [mess.uid] : true, active : false, fixed : false},
	      t : setTimeout(()=>{
		  data.videoStart = true;
		  clearTimeout(data.t) ;
		  this.activeVideoBlocks.push(data);
		  this.checkFixed(true);
		  this.appContext.appChangeRef.detectChanges();
	      }, mess.startTime - minTime)
	  };
	  return data
      });
  }

    checkFixed(type){
	this.activeVideoBlocks.length > 1 && this.activeVideoBlocks.forEach((data : any) => {
           data.local && (data.className.fixed = type);
       })
    }
  
  ngOnInit() {
  }

}
